@use '../utils'as *;

.nav {
  padding-right: 5%;
  width: 20%;
  gap: 40px;

  @include res(1200px) {
    width: 50%;
  }

  &__list {
    @include flex(space-between);

    .link-active {
      color: forestgreen;
      pointer-events: none;
    }

    &__item {
      &__link {
        @include header;
        color: greenyellow;

        @include res(800px) {
          font-size: 1em;
        }

        @include res(400px) {
          font-size: 0.6rem;
        }

        &:hover {
          opacity: 0.5;
        }
      }
    }
  }
}
