@use '../utils' as *;

.anim-exit {
  opacity: 1;
}

.anim-exit.anim-exit-active {
  opacity: 0;
  transition: opacity $animTime ease-in;
}

.anim-exit-done {
  opacity: 1;
}

.anim-enter {
  opacity: 0;
}

.anim-enter.anim-enter-active {
  opacity: 1;
  transition: opacity $animTime ease-in;
}

.anim-enter-done {
  opacity: 1;
}
